@use '../foundations/variables'as *;
@use '../foundations/mixins'as *;

.p-about {
    background: url(../../assets/images/common/bg-common01.png) no-repeat center / cover;
    @include padding-top-vw(220, 60);
    @include padding-bottom-vw(160, 80);

    @include sp-only {
        background-image: url(../../assets/images/common/sp/bg-common01.png);
    }

    .p-home-products {
        background: transparent;

        .l-inner {
            padding-bottom: 0;
            @include padding-top-vw(160,60);
        }
    }
}

.p-about-intro {
    position: relative;

    @include sp-only {}

    .l-inner {
        display: flex;
        justify-content: flex-end;
        @include padding-bottom-vw(160, 60);

        @include sp-only {
            display: block;
            @include padding-left-vw(0, 20);
            @include padding-right-vw(0, 20);
        }
    }

    .c-section-title {
        @include width-vw(600);
        @include margin-bottom-vw(20, 10);

        @include sp-only {
            width: 100%;
        }
    }


    &__media {
        position: absolute;
        top: 0;
        left: 0;
        @include width-vw(650);

        @include sp-only {
            position: static;
            width: 100%;
        }
    }

    &__content {
        @include padding-top-vw(30, 30);

        @include pc-only {
            @include min-height-vw(380);
        }

        @include sp-only {}
    }

    &__desc {
        color: $color-black;
        line-height: 2.2;
        @include width-vw(600);
        @include font-vw(16, 14);

        @include sp-only {
            width: 100%;
        }
    }
}

.p-about-benefit {
    .l-inner {
        @include padding-left-vw(0, 20);
        @include padding-right-vw(0, 20);
    }

    .c-section-title {
        @include margin-bottom-vw(40, 30);

        @include sp-only {}
    }

    &__list {}

    &__item {
        background-color: $color-white;
        display: flex;
        @include padding-vw(40, 40, 40, 40, 25, 25, 25, 25);
        @include border-radius-vw(10, 6);

        @include sp-only {
            display: block;
        }

        +.p-about-benefit__item {
            @include margin-top-vw(30, 30);

            @include sp-only {}
        }
    }

    &__media {
        overflow: hidden;
        @include width-vw(255, 255);
        @include border-radius-vw(6, 6);

        @include sp-only {
            width: 100%;
            @include margin-bottom-vw(0, 20);
        }
    }

    &__content {
        @include width-vw(860);
        @include padding-left-vw(30, 0);
        @include padding-top-vw(7, 0);

        @include sp-only {
            width: 100%;
        }
    }

    &__title {
        font-weight: $font-weight-bold;
        @include gradient-text;
        @include font-vw(26, 20);
        @include margin-bottom-vw(5, 5);

        @include sp-only {}
    }

    &__subtitle {
        font-weight: $font-weight-bold;
        position: relative;
        display: inline-block;
        @include margin-bottom-vw(10, 10);
        @include gradient-text;
        @include font-vw(16, 14);

        @include sp-only {}

        &::after {
            content: "";
            display: block;
            background: $gradation-brown;
            @include height-vw(1, 1);
            width: 100%;

            @include sp-only {}
        }
    }

    &__desc {
        font-weight: $font-weight-regular;
        line-height: 2.2;
        @include font-vw(16, 12);

        @include sp-only {}
    }

    &__desc-sub {
        font-weight: $font-weight-medium;
        line-height: 2.2;
        @include font-vw(16, 12);
    }
}

.p-about-routine {
    .c-section-title {
        @include margin-bottom-vw(30, 30);

        @include sp-only {}
    }

    .l-inner {
        @include padding-top-vw(140, 60);

        @include sp-only {
            @include padding-left-vw(0, 20);
            @include padding-right-vw(0, 20);
        }
    }
}