i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* Box-sizing を標準化 */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* HTML5 の要素を display: block に設定 */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

/* リンクの標準化 */
a {
  text-decoration: none;
  background-color: transparent;
  color: inherit;
  transition: 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
}

@media (min-width: 769px) {
  a:hover,
  a:focus {
    outline: none;
    opacity: 0.7;
  }
}

/* 画像のリセット */
img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  border-style: none;
}

/* フォーム要素の標準化 */
button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  border: none;
  background: none;
  padding: 0;
  appearance: none;
}

/* ボタンのデフォルトスタイル */
button {
  cursor: pointer;
}

button:disabled {
  cursor: default;
}

/* リストの標準化 */
ul,
ol {
  list-style: none;
}

/* テキストのリセット */
strong,
b {
  font-weight: bold;
}

em,
i {
  font-style: italic;
}

/* テーブルの標準化 */
table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
}

/* フォーム要素のリセット */
input,
button,
textarea,
select {
  outline: none;
}

textarea {
  resize: vertical;
}

input::placeholder,
textarea::placeholder {
  color: #999;
  opacity: 1;
}

/* フォーカス時のスタイルを強調（アクセシビリティ） */
:focus {
  outline: none;
}

/*
  ¥hidden 属性を適切に処理 */
[hidden] {
  display: none;
}

main {
  padding-top: 8.3333333333vw;
}
@media (max-width: 768px) {
  main {
    padding-top: 16vw;
  }
}

