/* ==========================================================================
   Fonts - Web フォントの定義 (Manrope & Zen Kaku Gothic New)
   ========================================================================== */
@font-face {
     font-family: 'Manrope';
     src: url('../../assets/fonts/Manrope-Light.woff2') format('woff2'),
          url('../../assets/fonts/Manrope-Light.woff') format('woff');
     font-weight: 300;
     font-style: normal;
     font-display: swap;
}

