@use '../foundations/variables'as *;
@use '../foundations/mixins'as *;

.l-products {

	&__list {
		&.--2column {
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;

			.l-products__item {
				@include pc-only {
					width: 50%;
				}

				&:nth-child(2) {
					@include pc-only {
						margin-top: 0;
					}
				}

				&:nth-child(n + 3) {
					@include pc-only {
						@include margin-top-vw(50);
					}

				}
			}

			.l-products__media {
								@include pc-only {
				@include width-vw(400);
								}
			}

			.l-products__body {
				@include pc-only {
					padding-right: 0;
					@include margin-left-vw(-30, 0);
				}

			}

		}
	}

	&__item {
		display: flex;
		align-items: center;
		justify-content: center;

		+.l-products__item {
			@include margin-top-vw(60, 50);
		}

		@include sp-only {
			display: block;
		}
	}

	&__media {
		@include width-vw(300);

		@include sp-only {
			width: 70%;
			margin-left: auto;
			margin-right: auto;
		}
	}

	&__body {
		@include width-vw(750);
		@include margin-left-vw(-20, 0);

		@include sp-only {
			width: 100%;
		}
	}

	&__kicker {
		font-weight: $font-weight-bold;
		position: relative;
		display: inline-block;
		@include gradient-text;
		@include font-vw(16, 14);
		@include margin-bottom-vw(12, 10);
		@include padding-right-vw(5, 5);
		@include padding-left-vw(5, 5);
		@include padding-bottom-vw(0, 0);

		@include sp-only {}

		&::after {
			content: "";
			display: block;
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			background: $gradation-brown;
			@include height-vw(1, 1);

			@include sp-only {}
		}
	}

	&__name {
		line-height: 1.2;
		@include margin-bottom-vw(10, 5);

		@include sp-only {}
	}

	&__name-en {
		display: block;
		font-weight: $font-weight-medium;
		@include font-vw(14, 12);
		@include gradient-text;

		@include sp-only {}
	}

	&__name-ja {
		display: block;
		font-weight: $font-weight-bold;
		@include margin-left-vw(-5, -2);
		@include font-vw(36, 20);
		@include gradient-text;

		@include sp-only {}
	}

	&__metas {
		display: flex;
		@include margin-bottom-vw(10, 10);

		@include sp-only {}
	}

	&__meta {
		background: $gradation-brown;
		color: $color-white;
		line-height: 1;
		font-size: 0;
		@include padding-vw(7, 10, 7, 10, 10, 10, 7, 10);

		@include sp-only {}

		+.l-products__meta {
			@include margin-left-vw(5, 5);

			@include sp-only {}
		}
	}

	&__size {
		font-weight: $font-weight-medium;
		display: inline-block;
		@include font-vw(12, 12);
		@include margin-right-vw(3, 5);

		@include sp-only {}
	}

	&__price {
		font-weight: $font-weight-medium;
		display: inline-block;
		@include font-vw(12, 12);

		@include sp-only {}

		small {
			@include font-vw(10, 10);
		}
	}

	&__tax {
		font-weight: $font-weight-medium;
		display: inline-block;
		@include font-vw(12, 12);

		@include sp-only {}
	}

	&__note {
		font-weight: $font-weight-regular;
		display: inline-block;
		@include font-vw(12, 12);

		@include sp-only {
			line-height: 1.3;
		}
	}

	&__desc {
		line-height: 2.2;
		font-weight: $font-weight-regular;
		@include font-vw(16, 14);

		@include sp-only {
			@include margin-bottom-vw(0, 20);
		}
	}

	&__actions {
		text-align: right;
		@include margin-top-vw(15, 0);

		.c-button {
			@include margin-top-vw(5, 5);

			@include sp-only {}

			&:last-of-type {
				@include margin-top-vw(7, 7);

				@include sp-only {}
			}
		}
	}
}