/* =====================================================================
   変形 / 影
   ===================================================================== */
@mixin translateY-vw($pc, $sp: null) {
  & {
    transform: translateY(px-to-vw($pc));
  }

  @if $sp !=null {
    @include sp-only {
      transform: translateY(sp-px-to-vw($sp));
    }
  }
}

@mixin translateX-vw($pc, $sp: null) {
  & {
    transform: translateX(px-to-vw($pc));
  }

  @if $sp !=null {
    @include sp-only {
      transform: translateX(sp-px-to-vw($sp));
    }
  }
}

@mixin box-shadow-vw($x-pc, $y-pc, $blur-pc, $spread-pc, $color,
  $x-sp: null, $y-sp: null, $blur-sp: null, $spread-sp: null) {
  & {
    box-shadow: px-to-vw($x-pc) px-to-vw($y-pc) px-to-vw($blur-pc) px-to-vw($spread-pc) $color;
  }

  @if $x-sp !=null and $y-sp !=null and $blur-sp !=null and $spread-sp !=null {
    @include sp-only {
      box-shadow: sp-px-to-vw($x-sp) sp-px-to-vw($y-sp) sp-px-to-vw($blur-sp) sp-px-to-vw($spread-sp) $color;
    }
  }
}

@mixin text-shadow-vw($x-pc, $y-pc, $blur-pc, $color,
  $x-sp: null, $y-sp: null, $blur-sp: null) {
  & {
    text-shadow: px-to-vw($x-pc) px-to-vw($y-pc) px-to-vw($blur-pc) $color;
  }

  @if $x-sp !=null and $y-sp !=null and $blur-sp !=null {
    @include sp-only {
      text-shadow: sp-px-to-vw($x-sp) sp-px-to-vw($y-sp) sp-px-to-vw($blur-sp) $color;
    }
  }
}


/* =====================================================================
   固定グラデーション – テキスト
   ===================================================================== */
@mixin gradient-text {
  background: $gradation-brown;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}

