@use '../foundations/variables'as *;
@use '../foundations/mixins'as *;

.p-news-detail {
    background: url(../../assets/images/common/bg-common01.png) no-repeat center / cover;
    @include padding-top-vw(220, 120);
    @include padding-bottom-vw(120, 80);

    @include sp-only {
        background-image: url(../../assets/images/common/sp/bg-common01.png);
    }

    @include sp-only {
        @include padding-left-vw(0, 20);
        @include padding-right-vw(0, 20);
    }

    .l-inner {}

    .c-section-title {
        @include margin-bottom-vw(60, 20);
    }

    &__content {}

    &__meta {
        display: flex;
        @include margin-bottom-vw(15, 10);
        @include pc-only {
        align-items: center;
        }
    }

    &__date {
        font-weight: $font-weight-regular;
        @include font-vw(24, 18);
        @include margin-right-vw(30, 20);
    }

    &__tags {
        font-size: 0;

    }

    &__tag {
        font-weight: $font-weight-regular;
        display: inline-block;
        @include border-vw(1, 1, solid, $color-black);
		@include margin-vw(10,10,10,5,5,5,5,5);
        @include padding-vw(3, 5, 3, 5, 3, 3, 3, 3);
        @include border-radius-vw(3, 3);
        @include font-vw(16, 12);

        @include sp-only {
            line-height: 1;
        }
    }

    &__title {
        font-weight: $font-weight-medium;
        @include font-vw(32, 26);
        @include margin-bottom-vw(20, 20);
    }

    &__thumb {
        overflow: hidden;
        @include margin-bottom-vw(30, 30);

        img {
            object-fit: cover;
            object-position: center;
            width: 100%;
            height: 100%;
        }
    }

    h2 {
        font-weight: $font-weight-medium;
        @include border-bottom-vw(1, 1, solid, $color-black);
        @include font-vw(24, 20);
        @include margin-bottom-vw(20, 20);
    }

    h3 {
        position: relative;
        font-weight: $font-weight-medium;
        @include font-vw(20, 16);
        @include margin-bottom-vw(10, 10);
        @include padding-left-vw(30, 30);

        &::before {
            content: "";
            display: block;
            background-color: $color-black;
            position: absolute;
            left: 0;
            @include top-vw(15, 15);
            @include width-vw(14, 14);
            @include height-vw(1, 1);
        }
    }

    h4 {
        font-weight: $font-weight-medium;
        @include margin-bottom-vw(10, 10);
    }

    p {
        font-weight: $font-weight-regular;
        line-height: 2.4;
        @include font-vw(16, 14);
        @include margin-bottom-vw(20, 20);
    }

    &__sns {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        @include margin-top-vw(40, 20);
        @include margin-bottom-vw(30, 30);
    }

    &__sns-text {
        font-weight: $font-weight-regular;
        position: relative;
        @include font-vw(14, 12);
        @include padding-right-vw(50, 50);
        @include padding-top-vw(5,5);

        &::after {
            content: "";
            display: block;
            background-color: $color-black;
            position: absolute;
            @include right-vw(15, 15);
            @include top-vw(17, 15);
            @include width-vw(14, 14);
            @include height-vw(1, 1);
        }
    }

    &__sns-list {
        display: flex;
    }

    &__sns-item {
        @include width-vw(30, 30);

        +.p-news-detail__sns-item {
            @include margin-left-vw(15, 15);
        }
    }

    &__nav-inner {
        display: flex;
        justify-content:  space-between;
        @include font-vw(14,14);
        @include margin-top-vw(50,50);
    }
}