@use '../foundations/variables'as *;
@use '../foundations/mixins'as *;

.p-contact {
    background: url(../../assets/images/common/bg-common01.png) no-repeat center / cover;
    @include padding-top-vw(220, 120);
    @include padding-bottom-vw(120, 80);
    @include sp-only {
        background-image: url(../../assets/images/common/sp/bg-common01.png);
    }

    .c-section-title {
        @include margin-bottom-vw(30, 10);
    }

    .l-inner {
        @include sp-only {
            @include padding-left-vw(0, 20);
            @include padding-right-vw(0, 20);
        }
    }

    .c-button {
        @include margin-top-vw(40, 20);
    }

    .l-table__th {
        @include sp-only {
            @include margin-bottom-vw(0, 10);
        }
    }
}

.p-thankyou {
    background: url(../../assets/images/common/bg-common01.png) no-repeat center / cover;
    @include padding-top-vw(220, 120);
    @include padding-bottom-vw(120, 80);
    @include sp-only {
        background-image: url(../../assets/images/common/sp/bg-common01.png);
    }

    .c-section-title {
        @include margin-bottom-vw(30, 10);
    }

    .l-inner {
        @include sp-only {
            @include padding-left-vw(0, 20);
            @include padding-right-vw(0, 20);
        }
    }

        .c-button {
        @include margin-top-vw(20, 20);
    }

    &__message {
        line-height: 2.2;
        text-align: center;
        @include font-vw(14,12);
        @include sp-only {}
    }
}