@use '../foundations/variables'as *;
@use '../foundations/mixins'as *;

.p-home-mv {
    &__content {
        position: absolute;
        left: 0;
        z-index: 1;
        @include bottom-vw(60, 0);

        @include sp-only {
            width: 100%;
            @include padding-vw(0, 0, 0, 0, 20, 20, 30, 20);
        }
    }

    &__title {
        color: $color-white;
        font-weight: $font-weight-bold;
        @include text-shadow-vw(0, 4, 6, $color-black-alpha-25, 0, 4, 6);
        @include font-vw(48, 36);

        @include sp-only {
            line-height: 1.4;
            @include margin-bottom-vw(0, 5);
        }
    }

    &__subtitle {
        color: $color-white;
        font-weight: $font-weight-medium;
        @include text-shadow-vw(0, 4, 6, $color-black-alpha-25, 0, 4, 6);
        @include font-vw(20, 18);
        @include margin-bottom-vw(20, 20);

        @include sp-only {}
    }
}

.p-home-concept {
    position: relative;
    background: url(../../assets/images/home/concept-bg.png) no-repeat center / cover;

    @include sp-only {
        background-image: url(../../assets/images/home/sp/concept-bg.png);
    }



    .c-section-title {
        @include sp-only {
            @include margin-bottom-vw(0, 20);
        }
    }

    .l-inner {
        display: flex;
        justify-content: flex-end;
        @include padding-top-vw(195, 50);
        @include padding-bottom-vw(120, 50);

        @include sp-only {
            display: block;
            @include padding-left-vw(0, 20);
            @include padding-right-vw(0, 20);
        }
    }

    &__image {
        position: absolute;
        left: 0;
        @include width-vw(650);
        @include top-vw(100, 50);

        @include sp-only {
            width: 100%;
            position: static;
            @include margin-bottom-vw(0, 20);
        }
    }

    &__content {
        @include width-vw(500);

        @include sp-only {
            width: 100%;
        }
    }

    &__text-wrap {}

    &__lead {
        font-weight: $font-weight-bold;
        @include gradient-text;
        @include font-vw(48, 32);
        @include margin-bottom-vw(20);

        @include sp-only {}
    }

    &__description {
        line-height: 2.4;
        font-weight: $font-weight-regular;
        @include font-vw(16, 14);

        @include sp-only {}

        .u-bold {
            @include gradient-text;
        }
    }
}

.p-home-products {
    background: url(../../assets/images/home/products-bg.png);

    @include sp-only {
        background-image: url(../../assets/images/home/sp/products-bg.png)
    }

    .l-inner {
        @include padding-top-vw(120, 60);
        @include padding-bottom-vw(120, 60);

        @include sp-only {
            @include padding-left-vw(0, 20);
            @include padding-right-vw(0, 20);
        }
    }

    .c-section-title {
        @include margin-bottom-vw(40, 30);

        @include sp-only {}
    }
}

.p-home-routine {
    background: url(../../assets/images/home/routine-bg.png) no-repeat center / cover;

    @include sp-only {
        background-image: url(../../assets/images/home/sp/routine-bg.png);
    }

    .l-inner {
        @include padding-top-vw(120, 60);
        @include padding-bottom-vw(120, 60);

        @include sp-only {
            @include padding-left-vw(0, 20);
            @include padding-right-vw(0, 20);
        }
    }

    .c-section-title {
        @include margin-bottom-vw(30, 30);

        @include sp-only {}
    }

    &__bundle {
        transition: all ease-in-out 0.3s;

        @include pc-only {
            &:hover {
                opacity: 0.7;
            }
        }
    }

    &__lead {
        font-weight: $font-weight-bold;
        text-align: center;
        position: relative;
        display: inline-block;
        @include margin-bottom-vw(40, 30);
        @include font-vw(32, 20);
        @include padding-left-vw(5, 5);
        @include padding-right-vw(5, 5);

        @include sp-only {
            line-height: 1;
            @include padding-bottom-vw(0, 5);
        }

        span {
            font-weight: $font-weight-medium;
            @include font-vw(56, 40);

            @include sp-only {}
        }

        &::after {

            content: "";
            display: block;
            position: absolute;
            left: 0;
            right: 0;
            background-color: $color-black;
            @include height-vw(1, 1);
            @include bottom-vw(8, 0);

            @include sp-only {}
        }
    }

    &__list {
        @include sp-only {
            @include margin-bottom-vw(0, 40);
        }
    }

    &__item {
        +.p-home-routine__item {
            @include margin-top-vw(80, 60);

            @include sp-only {}
        }
    }

    &__grid {
        @include margin-bottom-vw(60, 0);

        @include sp-only {
            position: relative;
            @include padding-bottom-vw(0, 40);
        }
    }

    &__content {}

    &__step-head {
        display: flex;
        position: relative;
        @include margin-bottom-vw(25, 15);

        @include sp-only {
            position: static;
        }
    }

    &__step {
        text-align: center;
        font-weight: $font-weight-bold;
        line-height: 1;
        position: relative;
        @include margin-left-vw(-30, -15);
        @include margin-right-vw(15, 8);
        @include width-vw(95, 75);

        @include sp-only {}

        &::after {
            content: ")";
            display: block;
            position: absolute;
            font-weight: $font-weight-light;
            right: 0;
            @include top-vw(-5, -5);
            @include font-vw(50, 40);

            @include sp-only {}
        }
    }

    &__step-label {
        display: block;
        @include font-vw(14, 12);

        @include sp-only {}
    }

    &__step-num {
        display: block;
        @include font-vw(30, 20);

        @include sp-only {}
    }

    &__title {
        font-weight: $font-weight-bold;
        @include font-vw(32, 20);

        @include sp-only {
            @include padding-top-vw(0, 3);
        }
    }

    &__subaction {
        position: absolute;
        right: 0;
        @include top-vw(5, 5);

        @include sp-only {
            top: 95%;
        }

        &:hover {
            .c-button__arrow {
                @include pc-only {
                    opacity: 1;
                    @include translateX-vw(5, 0);
                }
            }
        }
    }

    &__link {
        position: relative;
        font-weight: $font-weight-medium;
        color: $color-black;
        @include padding-left-vw(10, 10);
        @include font-vw(20, 14);
        @include padding-bottom-vw(3, 5);

        @include sp-only {}

        &::before {
            content: "";
            display: block;
            position: absolute;
            bottom: 0;
            background-color: $color-black;
            width: 100%;
            left: 0;
            @include height-vw(1, 1);

            @include sp-only {}
        }

        .c-button__arrow {
            display: block;
            position: absolute;
            background: url(../../../assets/images/common/arrow01-black-right.svg) no-repeat center / 100% 100%;
            right: 0;
            transition: $transition;
            @include bottom-vw(1, 1);
            @include width-vw(30, 30);
            @include height-vw(30, 30);

        }
    }

    &__link-note {
        font-weight: $font-weight-medium;
        position: relative;
        @include padding-right-vw(25, 25);
        @include padding-left-vw(10, 10);
        @include padding-bottom-vw(2, 2);
        @include font-vw(14, 12);

        @include sp-only {}

        // &::after {
        //     content: "";
        //     display: block;
        //     position: absolute;
        //     background: url(../../../assets/images/common/arrow01-black.svg) no-repeat center / 100% 100%;
        //     right: 0;
        //     transition: $transition;
        //     @include bottom-vw(-3, -3);
        //     @include width-vw(30, 30);
        //     @include height-vw(30, 30);

        //     @include sp-only {}
        // }

        // &:hover {
        //     @include pc-only {
        //         opacity: 1;

        //         &::after {
        //             @include translateX-vw(5, 0);
        //         }
        //     }
        // }
        &:hover {
            .c-button__arrow {
                @include pc-only {
                    opacity: 1;
                    @include translateX-vw(5, 0);
                }
            }
        }
    }

    &__desc {
        font-weight: $font-weight-regular;
        line-height: 2.2;
        @include font-vw(16, 14);
        @include margin-bottom-vw(10, 10);

        @include sp-only {}
    }

    &__point {
        display: inline-block;
        background: $gradation-brown;
        color: $color-white;
        line-height: 1;
        font-weight: $font-weight-regular;
        @include font-vw(14, 12);
        @include margin-bottom-vw(40, 0);
        @include padding-vw(5, 10, 5, 10, 5, 10, 5, 10);

        @include sp-only {

            line-height: 1.4;
            position: relative;
            @include padding-left-vw(0, 50);
        }

        span {
            @include font-vw(12, 10);
            @include margin-right-vw(5, 0);

            @include sp-only {
                position: absolute;
                @include left-vw(0, 10);
                @include top-vw(0, 5);
            }
        }
    }

    &__summary {
        @include margin-bottom-vw(20, 15);
        text-align: center;

        @include sp-only {}
    }

    &__summary-lead {
        font-weight: $font-weight-bold;
        @include font-vw(36, 22);

        @include sp-only {}
    }

    &__summary-num {
        font-weight: $font-weight-bold;
        @include font-vw(46, 32);

        @include sp-only {}
    }

    &__flow-wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        @include margin-bottom-vw(10, 10);

        @include sp-only {}
    }

    &__flow {
        display: flex;
        align-items: center;
        @include margin-right-vw(10, 5);

        @include sp-only {}
    }

    &__flow-item {}

    &__flow-arrow {
        @include margin-left-vw(10, 3);
        @include margin-right-vw(10, 3);

        @include sp-only {
            @include width-vw(0, 20);
        }
    }

    &__chip {
        color: $color-white;
        background: $gradation-brown;
        font-weight: $font-weight-medium;
        display: inline-block;
        @include font-vw(18, 12);
        @include padding-vw(5, 10, 5, 10, 4, 6, 4, 6);

        @include sp-only {}
    }

    &__to {
        font-weight: $font-weight-bold;
        @include font-vw(22, 14);

        @include sp-only {}
    }

    &__summary-desc {
        font-weight: $font-weight-bold;
        @include font-vw(22, 18);

        @include sp-only {}

        span {
            @include font-vw(28, 20);

            @include sp-only {}
        }
    }

    &__media {
        @include sp-only {
            @include margin-bottom-vw(0, 25);
        }
    }
}

.p-home-reviews {
    background: url(../../assets/images/home/reviews-bg.png) no-repeat center / cover;

    @include sp-only {
        background-image: url(../../assets/images/home/sp/reviews-bg.png);
    }

    .l-inner {
        @include padding-top-vw(120, 80);

        @include sp-only {
            @include padding-left-vw(0, 20);
            @include padding-right-vw(0, 20);
        }
    }

    .c-section-title {
        @include sp-only {
            @include margin-bottom-vw(0, 20);
        }
    }

    .swiper-wrapper {}

    &__slider {

        @include margin-top-vw(-60, -40);
        @include padding-bottom-vw(120, 80);

        @include sp-only {}
    }

    &__slide {
        transition: $transition;
        opacity: 0.3;
        @include margin-right-vw(30, 10);
        @include margin-left-vw(30, 10);
        @include width-vw(660);

        @include sp-only {
            width: 80%;
        }

        &.swiper-slide-active {
            opacity: 1;
        }
    }

    &__card {
        background-color: $color-white;
        position: relative;
        @include padding-vw(80, 60, 30, 60, 60, 30, 20, 30);
        @include box-shadow-vw(0, 4, 6, 0, $color-black-alpha-25, 0, 4, 6, 0);
        @include border-radius-vw(10, 6);

        @include sp-only {}
    }

    &__avatar {
        margin: auto;
        overflow: hidden;
        @include border-vw(1, 1, solid, $color-white);
        position: relative;
        z-index: 1;
        @include top-vw(60, 40);
        @include border-radius-vw(120, 80);
        @include width-vw(120, 80);
        @include height-vw(120, 80);

        @include sp-only {}
    }

    &__quote {
        font-weight: $font-weight-regular;
        line-height: 2;
        @include margin-bottom-vw(15, 15);
        @include font-vw(16, 12);

        @include sp-only {}
    }

    &__author {
        text-align: right;
        font-style: oblique;
        font-variation-settings: "slnt"-20;
        font-weight: $font-weight-medium;
        @include gradient-text;
        @include font-vw(16, 12);

        @include sp-only {}

        span {
            @include font-vw(24, 14);

            @include sp-only {}
        }
    }

    &__nav-wrap {
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        z-index: 1;
        @include top-vw(315, 250);
        @include width-vw(720);

        @include sp-only {
            width: 90%;
        }
    }

    &__nav {
        background: $gradation-brown;
        position: absolute;
        bottom: 0;
        margin: auto;
        @include border-vw(1, 1, solid, $color-white);
        @include border-radius-vw(70, 50);
        @include width-vw(70, 50);
        @include height-vw(70, 50);

        @include sp-only {}

        img {
            transition: $transition;
        }

        &.--prev {
            @include left-vw(-8, -8);

            &:hover {
                @include pc-only {
                    img {
                        @include translateX-vw(-5, 0);
                    }
                }
            }

            @include sp-only {}
        }

        &.--next {
            @include right-vw(-8, -8);

            &:hover {
                @include pc-only {
                    img {
                        @include translateX-vw(5, 0);
                    }
                }
            }

            @include sp-only {}
        }


    }
}

.p-home-faq {
    background: url(../../assets/images/home/qa-bg.png) no-repeat center /cover;

    @include sp-only {
        background-image: url(../../assets/images/home/sp/qa-bg.png);
    }

    .l-inner {
        @include padding-top-vw(120, 80);
        @include padding-bottom-vw(120, 80);

        @include sp-only {
            @include padding-left-vw(0, 20);
            @include padding-right-vw(0, 20);
        }
    }

    .c-section-title {
        @include margin-bottom-vw(60, 0);

        @include sp-only {}
    }

    &__list {}

    &__item {
        transition: $transition;
        cursor: pointer;
        @include border-bottom-vw(1, 1, solid, $color-black-alpha-25);
        @include padding-top-vw(45, 30);
        @include padding-bottom-vw(45, 30);

        @include sp-only {}
    }

    &__summary {
        position: relative;
        display: flex;

        @include sp-only {}
    }

    &__answer-wrap {
        display: none;
        @include margin-top-vw(35, 30);

        @include sp-only {}
    }

    &__qmark,
    &__amark {
        font-weight: $font-weight-regular;
        line-height: 1;
        @include width-vw(60, 35);
        @include gradient-text;
        @include font-vw(40, 30);
        @include margin-right-vw(15, 15);

        @include sp-only {}
    }

    &__question,
    &__answer {
        line-height: 2.2;
        font-weight: $font-weight-regular;
        color: $color-black;
        @include width-vw(835);
        @include font-vw(16, 14);
        @include padding-top-vw(5, 5);

        @include sp-only {
            width: 70%;
        }
    }

    &__note {
        display: inline-block;
        line-height: 1.8;
        @include font-vw(12, 12);

        @include sp-only {}
    }

    &__icon {
        color: $color-black;
        position: absolute;
        @include border-vw(1, 1, solid, $color-black);
        @include top-vw(8, 8);
        @include right-vw(10, 10);
        @include width-vw(30, 26);
        @include height-vw(30, 26);
        @include border-radius-vw(30, 26);

        @include sp-only {}

        div {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            background-color: $color-black;
            transition: $transition;
            @include height-vw(1, 1);
            @include width-vw(14, 10);

            @include sp-only {}

            &:nth-child(1) {}

            &:nth-child(2) {
                transform: rotate(90deg);

            }
        }

        &.is-active {
            div {
                &:nth-child(1) {
                    transform: rotate(180deg);
                }

                &:nth-child(2) {
                    opacity: 0;
                    transform: rotate(135deg);
                }
            }
        }
    }
}

.p-home-news {
    background: url(../../assets/images/home/news-bg.png) no-repeat center /cover;

    @include sp-only {
        background-image: url(../../assets/images/home/sp/news-bg.png);
    }

    .l-inner {
        @include padding-top-vw(120, 80);
        @include padding-bottom-vw(120, 80);

        @include sp-only {
            @include padding-left-vw(0, 20);
            @include padding-right-vw(0, 20);
        }
    }

    .c-section-title {
        @include margin-bottom-vw(20, 20);

        @include sp-only {}
    }

    &__wrap {
        display: flex;
        justify-content: space-between;

        @include sp-only {
            display: block;
        }
    }

    &__head {
        @include sp-only {
            display: flex;
            align-items: center;
        }

        .c-button {
            @include sp-only {
                margin-left: auto;
            }
        }
    }
}