@use '../foundations/variables'as *;
@use '../foundations/mixins'as *;

.l-news {
	&__list {
		@include width-vw(930);

		@include sp-only {
			width: 100%;
		}

		&.--lg {
			width: 100%;

			.l-news__body {
				@include width-vw(970);

				@include sp-only {
					width: 70%;
				}
			}
		}
	}

	&__item {
		position: relative;
		@include border-bottom-vw(1, 1, solid, $color-black-alpha-25);
	}

	&__link {
		display: flex;
		cursor: pointer;
		transition: $transition;
		@include padding-top-vw(35, 20);
		@include padding-bottom-vw(35, 20);
		@include padding-left-vw(20, 10);

		@include sp-only {
			@include padding-right-vw(0, 10);
		}

		@include pc-only {
			&:hover {
				opacity: 1;

				.l-news__arrow {
					img {
						@include translateX-vw(3, 0);
					}
				}
			}
		}
	}

	&__thumb {
		overflow: hidden;
		@include width-vw(178);
		@include height-vw(100, 60);
		@include border-radius-vw(6, 3);
		@include margin-right-vw(35, 0);

		@include sp-only {
			width: 30%;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
		}
	}

	&__body {
		@include padding-top-vw(15, 5);
		@include padding-right-vw(60, 0);
		@include width-vw(690);

		@include sp-only {
			width: 70%;
			@include padding-left-vw(0, 15);
		}
	}

	&__meta {
		display: flex;
		align-items: center;

		@include sp-only {
			display: block;
		}
	}

	&__date {
		font-weight: $font-weight-light;
		@include margin-right-vw(15, 0);
		@include font-vw(20, 12);

		@include sp-only {
			line-height: 1;
		}
	}

	&__tags {
		font-size: 0;
		        @include sp-only {
            @include margin-left-vw(0,-5);
        }
	}

	&__tag {
		font-weight: $font-weight-regular;
		display: inline-block;
		@include border-vw(1, 1, solid, $color-black);
		@include margin-vw(10,10,10,0,5,5,5,5);
		@include padding-vw(2, 8, 2, 8, 2, 3, 2, 3);
		@include border-radius-vw(3, 3);
		@include font-vw(12, 10);

		@include sp-only {
			line-height: 1;
		}
	}

	&__title {
		font-weight: $font-weight-regular;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 1;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: normal;
		word-break: break-word;
		@include font-vw(18, 11);

		@include sp-only {}
	}

	&__arrow {
		position: absolute;
		display: flex;
		align-items: center;
		justify-content: center;
		@include border-vw(1, 1, solid, $color-black);
		@include width-vw(30, 30);
		@include height-vw(30, 30);
		@include border-radius-vw(30, 30);
		@include right-vw(16, 16);
		@include top-vw(70, 70);

		@include sp-only {
			display: none;
		}

		img {
			transition: $transition;
		}
	}

	&__desc {
		font-weight: $font-weight-regular;
		line-height: 2.2;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 1;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: normal;
		word-break: break-word;
		@include font-vw(12, 10);

		@include sp-only {
			display: none;
		}

	}
}

.l-news-pagenation {
	display: flex;
	justify-content: center;
	@include margin-top-vw(60, 30);

	&__item {
		pointer-events: all;
		display: flex;
		align-items: center;
		justify-content: center;
		font-weight: $font-weight-regular;
		@include border-vw(1, 1, solid, $color-black);
		@include margin-left-vw(10, 5);
		@include margin-right-vw(10, 5);
		@include width-vw(46, 38);
		@include height-vw(46, 38);
		@include border-radius-vw(46, 38);
		@include font-vw(18, 12);

		&.--arrrow {}

		&.--dot {
			@include padding-bottom-vw(6,6);
		}

		&.is-active {
			color: $color-white;
			background-color: $color-black;
		}
	}
}