@use '../foundations/variables'as *;
@use '../foundations/mixins'as *;

.l-table {
	&__tr {
		@include border-bottom-vw(1, 1, solid, $color-black-alpha-25);
	}

	&__th {
		text-align: left;
		position: relative;
		vertical-align: top;
		@include padding-left-vw(25, 10);
		@include padding-right-vw(10, 10);
		@include padding-top-vw(35, 30);
		@include padding-bottom-vw(35, 0);

		@include sp-only {
			display: block;
		}
	}

	&__th-content {
			@include padding-top-vw(13, 0);
	}

	&__td {
		text-align: left;
		position: relative;
		@include padding-left-vw(25, 10);
		@include padding-right-vw(10, 10);
		@include padding-top-vw(35, 0);
		@include padding-bottom-vw(35, 30);

		@include sp-only {
			display: block;
		}

		&.--select {
			&::after {
				content: "";
				display: block;
				background: url(../../assets/images/common/arrow03-black-bottom.svg) no-repeat center / 100% 100%;
				position: absolute;
				top: 0;
				bottom: 0;
				margin: auto;
				z-index: 1;
				pointer-events: none;
				@include left-vw(260);
				@include width-vw(20, 20);
				@include height-vw(20, 20);

				@include sp-only {
					left: auto;
					@include top-vw(0,-15);
						@include right-vw(0,25);
				}
			}
		}
	}

	&__th-text01 {
		font-weight: $font-weight-medium;
		@include font-vw(16, 14);
	}

	&__th-text02 {
		font-weight: $font-weight-medium;
		color: $color-red;
		position: absolute;
		@include top-vw(45, 28);
		@include right-vw(10, 10);
		@include padding-vw(3, 8, 3, 8, 3, 6, 3, 6);
		@include border-radius-vw(3, 3);
		@include border-vw(1, 1, solid, $color-red);
		@include font-vw(11, 11);

		@include sp-only {

		}
	}

	&__td-text01 {
		font-weight: $font-weight-regular;
		@include font-vw(16, 14);
	}

	input {
		background-color: $color-white;
		width: 100%;
		display: flex;
		align-items: center;
		@include font-vw(14, 14);
		@include border-vw(1, 1, solid, $color-black-alpha-25);
		@include border-radius-vw(6, 6);
		@include height-vw(46, 46);
		@include padding-vw(10, 10, 10, 10, 10, 10, 10, 10);

		@include sp-only {
			width: 100%;
		}
	}

	select {
		background-color: $color-white;
		width: 50%;
		display: flex;
		align-items: center;
		position: relative;
		@include font-vw(14, 14);
		@include border-vw(1, 1, solid, $color-black-alpha-25);
		@include border-radius-vw(6, 6);
		@include height-vw(46, 46);
		@include padding-vw(10, 10, 10, 10, 10, 10, 10, 10);

		@include sp-only {
			width: 100%;
		}
	}

	textarea {
		background-color: $color-white;
		width: 100%;
		@include min-height-vw(250, 200);
		@include font-vw(14, 14);
		@include border-vw(1, 1, solid, $color-black-alpha-25);
		@include border-radius-vw(6, 6);
		@include padding-vw(10, 10, 10, 10, 10, 10, 10, 10);

		@include sp-only {
			width: 100%;
		}
	}
}